<template>
  <b-card
    :title="name"
    :sub-title="getDate"
    :img-src="getUrlPath"
    img-alt="Image"
    img-top
    tag="article"
    class="mb-2"
  >
    <b-card-text>
      {{desc}}
    </b-card-text>


    <b-button v-b-modal="'modal' + order" variant="primary">Show Projects</b-button>
    <b-modal :id="'modal'+order" :title="name">
      <b-row>
          <b-col xl="12" md="12" v-for="(item, idx) in project" :key="idx">
            <p>
              <span>Name: {{item.name}}</span><br>
              <span>Desc: {{item.desc}}</span><br>
              <span>Skill: {{item.skill}}</span>
              <hr>
            </p>
          </b-col>
      </b-row>
    </b-modal>
  </b-card>
</template>
<script>
  export default {
    name: "card-detail",
    props: {
      order: Number,
      name: String,
      startDate: String,
      endDate: String,
      desc: String,
      logo: String,
      project: Object
    },
    computed: {
      getUrlPath() {
        return require(`@/assets/images/logo/${this.logo}`);
      },
      getDate() {
        return `${this.startDate}~${this.endDate}`
      },
    }
  };
</script>
<style scoped>
img {
  padding-top: calc(15%);
  width: 200px;
  height: 150px;
  background: #ffffff;
  margin: 0 auto;

  max-width: 200px;
  width: 100%;
  display: inline-block;
}

</style>
